export default (path, options) => {
  const { placeholder = '' } = options
  return {
    component: 'input',
    model: [
      path,
      { type: 'number', on: 'change', handler: evt => evt.target.value }
    ],
    fieldOptions: {
      class: 'form-control',
      domProps: { type: 'number', placeholder }
    }
  }
}
